/**
* @file input 基础组件
* @author fuqiangqiang@baidu.com
*/

import san, {DataTypes} from 'san';
import keyCode from '../core/util/keyCode';
import {classCreator} from '../core/util';
const prefixCls = classCreator('input')();

export default san.defineComponent({
    dataTypes: {
        size: DataTypes.string,
        disabled: DataTypes.bool
    },
    initData() {
        return {
            type: 'text'
        };
    },
    inputChange(e) {
        this.fire('change', e.target.value);
        this.dispatch('UI:form-item-interact', {fieldValue: e.target.value, type: 'change', e});
    },
    keydownHander(e) {
        if (e.keyCode === keyCode.ENTER) {
            this.fire('pressEnter', e.target.value);
            this.dispatch('UI:form-item-interact', {fieldValue: e.target.value, type: 'change', e});
        }
    },
    inputOnBlur(e) {
        this.fire('blur', e.target.value);
        this.dispatch('UI:form-item-interact', {fieldValue: e.target.value, type: 'blur', e});
    },
    focus() {
        this.ref('input').focus();
    },
    blur() {
        this.ref('input').blur();
    },
    template: `
        <input
            placeholder="{{placeholder}}"
            class="${prefixCls} {{size ? '${prefixCls}-' + size : ''}} {{disabled ? '${prefixCls}-disabled' : ''}} {{inputClasses}}"
            on-input="inputChange($event)"
            on-keydown="keydownHander($event)"
            on-blur="inputOnBlur($event)"
            value="{=value=}"
            disabled="{{disabled}}"
            readonly="{{readOnly}}"
            id="{{id}}"
            type="{{type}}"
            tabindex="{{tabIndex}}"
            maxlength="{{maxLength}}"
            autocomplete="{{autocomplete}}"
            s-ref="input"
        />
    `
});
